    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
/*@import url("https://fonts.googleapis.com/css2?family=Kosugi+Maru&display=swap");*/
.home-dropDown {
  font-family: "RocknRoll One";
}

.full-screen {
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
  background: rgb(34, 178, 195);
  background: linear-gradient(
    0deg,
    rgba(34, 178, 195, 1) 0%,
    rgba(153, 45, 253, 1) 100%
  );
  position: fixed;
  display: grid;
  grid-template-rows: 50px 230px 40px auto;

}



.home-title {
  height: 50px;
  /*font-family: "Kosugi Maru";*/
  font-family: "RocknRoll One";  
  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 30px;
  line-height: 2em;
  color: rgb(255, 255, 255);
}
.shadow {
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);
}
.abs-center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.home-center-container {
  /*position: absolute;
  max-height: 30%;
  max-width: 50%;*/

  width: 500px;
  padding: 5px 20px;
  margin: 0 auto;
  border-radius: 8px;

  background-color: #fff6de;
  display: flex;
  flex-direction: column;
}

.form-item {
  height: 35px;

  color: #455769;
  border-radius: 3px;

  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 3px 0;
}
.form-item p {
  min-width: 50px;
  text-align: center;
  font-family: "RocknRoll One";
}
.form-item-input {
  height: 50%;
  width: 30px;
  text-indent: 0.3em;
  text-align: center;
  font-size: medium;

  display: grid;
  place-items: center;
  border: 5px solid;

  border-image: linear-gradient(to bottom, turquoise, greenyellow) 0 1;
}

.form-item-full-button {
  width: 70%;
  background-color: black;
  color: aliceblue;
  font-family: "RocknRoll One";
  height: 30px;
  display: flex;
  margin: 0 auto;
  margin-top: 10px;
  justify-content: center;
  align-items: center;
  border-radius: 5px;

  cursor: pointer;

  -webkit-animation-iteration-count: infinite;

          animation-iteration-count: infinite;
  transition: all 0.1s ease-in-out;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-name: shake;
          animation-name: shake;
}

.home-main-seg {
  width: 100%;
  /*height: 65%;*/
  margin: 10px 0;
}

.home-socialbtn-group{
    display: flex;
    flex-direction: row;
    margin:  0 auto;
    height: 100%;    
    background-color: unset;
    
}
.home-socialbtn-group button{    
    background-color: unset;
    border: none;    
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.home-socialbtn-group button img{
    max-height: 20px;
    border-radius: 50px;
}

@-webkit-keyframes shake {
  0% {
    -webkit-transform: rotate(0turn);
            transform: rotate(0turn);
  }
  10% {
    -webkit-transform: rotate(0.0051turn);
            transform: rotate(0.0051turn);
  }
  25% {
    -webkit-transform: rotate(-0.0051turn);
            transform: rotate(-0.0051turn);
  }
  40% {
    -webkit-transform: rotate(0turn);
            transform: rotate(0turn);
  }
}

@keyframes shake {
  0% {
    -webkit-transform: rotate(0turn);
            transform: rotate(0turn);
  }
  10% {
    -webkit-transform: rotate(0.0051turn);
            transform: rotate(0.0051turn);
  }
  25% {
    -webkit-transform: rotate(-0.0051turn);
            transform: rotate(-0.0051turn);
  }
  40% {
    -webkit-transform: rotate(0turn);
            transform: rotate(0turn);
  }
}

.call-me-btn-root {
  position: fixed;
  bottom: 30px;
  right: 35px;
  font-family: "RocknRoll One";
  line-height: 15px;
  font-size: smaller;
}
.call-me-btn-root img {
  position: absolute;
  bottom: 0;
  right: 40px;
  height: 50px;
  border-radius: 50%;
  background-color: white;
  border: 5px double black;

  margin-top: 15px;
  margin-left: 50px;
  cursor: pointer;
}

@keyframes popout {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes popout {
  from {
    -webkit-transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
  }
}
.call-me-btn-root:hover > .dialog {
  display: flex;
  -webkit-animation: popout 0.7s ease;
          animation: popout 0.7s ease;
}

.dialog {
  
  bottom: 5.5rem;
  background-color: #1c3d5a;
  border-radius: 0.25rem;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  margin-left: 0.5rem;
  margin-right: 0.0rem;
  width: 130px;
  margin: 0 auto;
  margin-bottom: 70px;
  /*animation: popout 0.7s ease;*/
}

.dialog ul {
  list-style: none;
  padding: 0;
  width: 100%;
}

.dialog li {
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 1rem;
}

.dialog li a {
  color: #fff;
  text-decoration: none;
}

.arrow {
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #1c3d5a;
  position: relative;
  top: 0.6rem;
}

.ilcb-root {
  display: flex;
  flex-direction: row;
  margin: 0 auto;

  width: 90%;
  height: 90%;

  justify-content: space-evenly;
  align-content: stretch;
}

.ilcb-item {
  cursor: pointer;
  border: 3px solid black;

  height: 90%;
  width: 90%;
  display: flex;
  margin: 0 1px;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  font-family: "RocknRoll One";

  transition: all 0.2s ease-out;
}
.ilcb-item:hover {
  -webkit-transform: rotate(0.01turn) scale(1.1);
          transform: rotate(0.01turn) scale(1.1);
  background: rgb(34, 186, 195);
  background: linear-gradient(
    0deg,
    rgba(34, 186, 195, 1) 0%,
    rgba(238, 253, 45, 1) 100%
  );
}

.ilcb-selected {
  background: rgb(34, 186, 195);
  background: linear-gradient(
    0deg,
    rgba(34, 186, 195, 1) 0%,
    rgba(238, 253, 45, 1) 100%
  );
  -webkit-transform: rotate(-0.01turn) scale(0.9);
          transform: rotate(-0.01turn) scale(0.9);
}

